.miguLogoWhite {
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
}
.miguLogoBlack {
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
}
.miguNavWhite {
  background: #FFF;
  color: #666;
}
.miguNavBlack {
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
}
@media screen and (max-width: 1640px) {
  .nav {
    width: 100%;
    height: 60px;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  }
  .nav .nav-container {
    width: 1160px;
    height: 60px;
    margin: 0 auto;
  }
  .nav .nav-container ul li {
    float: left;
    padding: 0 10px;
    font-size: 14px;
    cursor: pointer;
  }
  .nav .nav-container ul li div {
    position: relative;
    height: 50px;
    line-height: 60px;
  }
  .nav .nav-container ul li div.active {
    color: #007AFF;
    font-weight: bold;
  }
  .nav .nav-container ul li div.active::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 4px;
    background: #007AFF;
    border-radius: 2px;
  }
  .nav .nav-container ul li:nth-child(1) {
    padding-left: 0;
  }
}
@media screen and (min-width: 1640px) {
