@media screen and (max-width: 1640px) {
  .render-temp {
    width: 1160px;
    height: 56px;
    margin: 0 auto;
    padding-top: 20px;
    box-sizing: border-box;
  }
  .render-temp .header {
    height: 24px;
    margin-bottom: 12px;
  }
  .render-temp .header span {
    display: inline-block;
    text-align: left;
  }
  .render-temp .header span:nth-child(1) {
    margin-right: 12px;
    font-size: 18px;
    color: #333;
    letter-spacing: 0.78px;
    font-weight: bold;
  }
}
@media screen and (min-width: 1640px) {
  .render-temp {
    width: 1640px;
    height: 58px;
    margin: 0 auto;
    padding-top: 20px;
    box-sizing: border-box;
  }
  .render-temp .header {
    height: 24px;
    margin-bottom: 12px;
  }
  .render-temp .header span {
    display: inline-block;
    text-align: left;
  }
  .render-temp .header span:nth-child(1) {
    margin-right: 12px;
    font-size: 20px;
    color: #333;
    letter-spacing: 0.87px;
    font-weight: bold;
  }
}
