@media screen and (max-width: 1640px) {
  .render-content {
    width: 1160px;
    height: 380px;
    font-size: 0;
    margin: 0 auto;
  }
  .render-content > div {
    display: inline-block;
  }
  .render-content .left {
    vertical-align: bottom;
    width: 440px;
    height: 380px;
    margin-right: 10px;
    background: #F2F2F2;
    cursor: pointer;
  }
  .render-content .left .logo-big {
    height: 344px;
  }
  .render-content .left .logo-big img {
    width: 440px;
    height: 344px;
  }
  .render-content .left p {
    height: 36px;
    line-height: 36px;
    font-size: 14px;
    letter-spacing: 0.61px;
    color: #151515;
    text-align: left;
    text-indent: 1em;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .render-content .right {
    width: 710px;
    height: 380px;
    overflow: hidden;
  }
  .render-content .right ul li:nth-child(3n) {
    margin-right: 0px;
  }
  .render-content .right ul li {
    width: 230px;
    height: 185px;
    float: left;
    background: #F2F2F2;
    margin: 0 9px 10px 0;
    cursor: pointer;
  }
  .render-content .right ul li .logo-small {
    width: 230px;
    height: 152px;
    position: relative;
  }
  .render-content .right ul li .logo-small img {
    width: 100%;
    height: 152px;
  }
  .render-content .right ul li .logo-small span {
    width: 50px;
    height: 20px;
    line-height: 20px;
    position: absolute;
    right: 7.5px;
    bottom: 8px;
    font-size: 12px;
    color: #FFF;
    letter-spacing: 0.52px;
    text-align: center;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 17px;
  }
  .render-content .right ul li p {
    height: 33.5px;
    line-height: 33.5px;
    text-align: left;
    text-indent: 1em;
    font-size: 12px;
    color: #151515;
    letter-spacing: 0.52px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
@media screen and (min-width: 1640px) {
  .render-content {
    width: 1640px;
    height: 420px;
    font-size: 0;
    margin: 0 auto;
  }
  .render-content > div {
    display: inline-block;
  }
  .render-content .left {
    vertical-align: top;
    width: 560px;
    height: 420px;
    margin-right: 20px;
    background: #F2F2F2;
    cursor: pointer;
  }
  .render-content .left .logo-big {
    height: 370px;
  }
  .render-content .left .logo-big img {
    width: 560px;
    height: 370px;
  }
  .render-content .left p {
    height: 50px;
    line-height: 50px;
    font-size: 22px;
    letter-spacing: 0.96px;
    color: #151515;
    text-align: left;
    text-indent: 1em;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .render-content .right {
    width: 1060px;
    height: 420px;
    overflow: hidden;
  }
  .render-content .right ul li:nth-child(4n) {
    margin-right: 0px;
  }
  .render-content .right ul li {
    width: 250px;
    height: 200px;
    float: left;
    background: #F2F2F2;
    margin: 0 20px 20px 0;
    cursor: pointer;
  }
  .render-content .right ul li .logo-small {
    width: 250px;
    height: 170px;
    position: relative;
  }
  .render-content .right ul li .logo-small img {
    width: 100%;
    height: 170px;
  }
  .render-content .right ul li .logo-small span {
    width: 68px;
    height: 30px;
    line-height: 30px;
    position: absolute;
    right: 7.5px;
    bottom: 8px;
    font-size: 18px;
    color: #FFF;
    letter-spacing: 0.78px;
    text-align: center;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 17px;
  }
  .render-content .right ul li p {
    height: 30px;
    line-height: 30px;
    text-align: left;
    text-indent: 1em;
    font-size: 16px;
    color: #151515;
    letter-spacing: 0.7px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
