.sports-model {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
}
.zhu-window {
  width: 330px;
  height: 220px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: url(../assets/world_code.png);
  background-size: 100% 100%;
}
.sure {
  width: 98px;
  height: 34px;
  position: absolute;
  top: 91%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: url(data:image/png;base64,);
  background-size: 100% 100%;
  cursor: pointer;
}
