  }
  .carousel .swiper-container .swiper-wrapper .swiper-slide .container-bg {
    width: 1200px;
    margin: 0 auto;
    position: relative;
    cursor: pointer;
  }
  .carousel .swiper-container .swiper-wrapper .swiper-slide .container-bg .real-bg {
    width: 100%;
    height: 600px;
  }
  .carousel .swiper-container .swiper-wrapper .swiper-slide .container-bg .content-name {
    position: absolute;
    left: 40px;
    bottom: 40px;
    font-size: 22px;
    color: #FFF;
    letter-spacing: 0.96px;
    text-align: left;
    font-weight: bold;
    cursor: pointer;
  }
  .carousel .swiper-container .swiper-wrapper .swiper-slide .vague-bg {
    width: 100%;
    height: 600px;
    position: absolute;
    top: 0;
    left: 0;
    filter: blur(8px);
    -webkit-filter: blur(8px);
    z-index: -1;
  }
  .carousel .swiper-container .swiper-pagination {
    position: absolute;
    left: 50%;
    bottom: 20px;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .carousel .swiper-container .swiper-pagination > .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    margin: 0 7px;
    background: #fff;
    opacity: 1;
  }
  .carousel .swiper-container .swiper-pagination > .swiper-pagination-bullet-active {
    background: #007aff;
    opacity: 1;
  }
  .carousel .swiper-container .swiper-button-prev {
    width: 70px;
    height: 70px;
    left: 230px;
    right: 'auto';
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
  }
  .carousel .swiper-container .swiper-button-next {
    width: 70px;
    height: 70px;
    left: 'auto';
    right: 230px;
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
  }
}
